import { Location } from '@angular/common';
import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, UntypedFormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import {
  DataUrl,
  NgxImageCompressService,
  UploadResponse,
} from 'ngx-image-compress';
import { Subscription } from 'rxjs';
import { OrdenCompraPost } from 'src/app/interfaces/orden-compra.interface';
import { Proveedor } from 'src/app/interfaces/proveedor.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { OrdenesService } from 'src/app/services/ordenes.service';
import { ProveedoresService } from 'src/app/services/proveedores.service';

@Component({
  selector: 'app-albaran',
  templateUrl: './albaran.component.html',
  styleUrls: ['./albaran.component.scss'],
})
export class AlbaranComponent implements OnInit, OnDestroy {
  // servicios requeridos
  private readonly OrdenesService = inject(OrdenesService);
  private readonly proveedoresService = inject(ProveedoresService);
  private readonly router = inject(Router);
  private readonly messageService = inject(CustomMessageService);
  private readonly Form = inject(FormBuilder);
  private readonly location = inject(Location);
  private readonly imageCompress = inject(NgxImageCompressService);

  public compuestoSnOptions: Object[] = [
    { value: 'Si', label: 'Si' },
    { value: 'No', label: 'No' },
  ];

  public imagenSubida: any;

  public proveedores: Proveedor[] = [];

  public loadingProveedores = true;

  public hadSubmit: boolean = false;

  public loading = false;

  public subs: Subscription[] = [];

  public newOrdenCompra = this.Form.group({
    fechaEjecucion: [null, Validators.required],
    proveedor: [null, Validators.required],
    numero: [null, Validators.required],
    imagen: [null],
    tipo: ['Albaran',Validators.required],
  });

  // form getters ----------------------------------------------------------------------------------

  get fechaEjecucion() {
    return this.newOrdenCompra.get('fechaEjecucion') as UntypedFormControl;
  }
  get proveedor() {
    return this.newOrdenCompra.get('proveedor') as UntypedFormControl;
  }
  get numero() {
    return this.newOrdenCompra.get('numero') as UntypedFormControl;
  }
  get imagen() {
    return this.newOrdenCompra.get('imagen') as UntypedFormControl;
  }

  // end form getter --------------------------------------------------------------------------------

  ngOnInit(): void {
    this.cargarProveedores();
  }

  back() {
    this.router.navigateByUrl('/');
  }

  validar(campo: string) {
    return this.newOrdenCompra.get(campo)?.valid;
  }

  // dinamica de los componentes -------------------------------------------------------------------

  submit() {
    this.hadSubmit = true;

    this.loading = true;

    if (this.newOrdenCompra.invalid) {
      console.log(this.newOrdenCompra.invalid);

      this.loading = false;
      this.messageService.error('Verifique los campos');
      return;
    }

    let ordenCompraToSend: OrdenCompraPost = this.newOrdenCompra
      .value as OrdenCompraPost;

    ordenCompraToSend.imagen = this.imagenSubida;

    this.reqCreateOrdenCompra(ordenCompraToSend);
  }

  // llamadas al back -----------------------------------------------------------------------------

  reqCreateOrdenCompra(ordenCompraToSend: OrdenCompraPost) {
    this.subs.push(
      this.OrdenesService.createOrdenCompra(ordenCompraToSend).subscribe(
        (response) => {
          console.log(response);
          this.messageService.add({
            severity: 'success',
            summary: 'Exito!',
            detail: 'Factura creado con exito!',
          });
          console.log(response);
          this.router.navigateByUrl('/confirmar-orden-compra');
        },
        (error) => {
          console.error(error);

          this.loading = false;
          this.messageService.error(error.error.detail);
          return;
        }
      )
    );
  }

  // cargo los tipos de productos del select homonimo
  cargarProveedores() {
    this.proveedoresService.getProveedores().subscribe({
      next: (response2) => {
        this.proveedores = response2;
        this.loadingProveedores = false;
      },
      error: (error) => {
        console.error(error);
        this.messageService.error(error.error.detail);
        return;
      },
    });
  }

  uploadAndResize() {
    return this.imageCompress
      .uploadFile()
      .then(({ image, orientation }: UploadResponse) => {
        console.warn(
          'el tamano era: ',
          this.imageCompress.byteCount(image),
          ' bytes'
        );
        console.warn(
          'Comprimiendo and redimencionando a un tamano de 244x244px...'
        );

        this.imageCompress
          .compressFile(image, orientation, 50, 50, 244, 244)
          .then((result: DataUrl) => {
            this.imagenSubida = result;
            console.warn(
              'El tamano ahora es:',
              this.imageCompress.byteCount(result),
              ' bytes'
            );
          });
      });
  }

  onRemoveImg() {
    this.imagen.setValue(null);
    this.imagenSubida = null;
  }

  ngOnDestroy(): void {
    this.subs.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }
}
