import {
  Component,
  OnInit,
  OnDestroy,
  inject,
  Input,
  EventEmitter,
  Output,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuItem } from 'primeng/api';
import { LineaTarea } from 'src/app/interfaces/tarea.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { PruebasService } from 'src/app/services/pruebas.service';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  selector: 'app-mostrar-tarea-tiempo',
  templateUrl: './mostrar-tarea-tiempo.component.html',
  styleUrls: ['./mostrar-tarea-tiempo.component.scss'],
})
export class MostrarTareaTiempoComponent implements OnInit, OnDestroy {
  private readonly tareaService = inject(TareaService);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly messageService = inject(CustomMessageService);
  // private readonly pruebasService = inject(PruebasService);

  cardMenu: MenuItem[];

  @Input() tarea: LineaTarea;

  @Input() estadoTareaIniciada!: boolean;

  @Output() cambioEstadoTareaInciada = new EventEmitter<boolean>();

  subcripciones = [];

  tiempos = [];
  totaltime = '';

  loadingTiempos = false;

  ngOnInit(): void {
    this.subcripciones.push(
      this.tareaService.getEstadoTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          console.log(response);
          this.estadoTareaIniciada = response.tareaIniciada;
        },
        error: (error) => {
          error.error?.detail
            ? this.messageService.error(error.error.detail)
            : error.error.message == 'JWT Token not found'
            ? this.messageService.alert('Sesion Expirada')
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
        },
      })
    );

    let aux = JSON.parse(JSON.stringify(this.tarea))

    console.log(aux);

    this.cargartiempos();
  }

  empezarTarea() {
    if (!this.estadoTareaIniciada) {
      this.tareaService.startTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'la tarea a sido iniciada',
          });
          this.cambioEstadoTareaInciada.emit(this.estadoTareaIniciada);
          this.ngOnInit();
        },
        error: (error) => {
          error.error?.detail
            ? this.messageService.error(error.error.detail)
            : error.error.message == 'JWT Token not found'
            ? this.messageService.alert('Sesion Expirada')
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
        },
      });
    }
  }

  pausarTarea() {
    if (this.estadoTareaIniciada) {
      this.tareaService.pauseTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'la tarea a sido pausada',
          });
          this.cambioEstadoTareaInciada.emit(this.estadoTareaIniciada);
          this.ngOnInit();
        },
        error: (error) => {
          error.error?.detail
            ? this.messageService.error(error.error.detail)
            : error.error.message == 'JWT Token not found'
            ? this.messageService.alert('Sesion Expirada')
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
        },
      });
    }
  }

  detenerTarea() {
    if (this.estadoTareaIniciada) {
      this.tareaService.stopTimeTarea(this.tarea.id).subscribe({
        next: (response) => {
          this.messageService.add({
            severity: 'success',
            summary: 'Exito',
            detail: 'la tarea a sido pausada',
          });
          this.cambioEstadoTareaInciada.emit(this.estadoTareaIniciada);
          this.tarea.estado = 'finalizado'
          this.cargartiempos()
        },
        error: (error) => {
          error.error?.detail
            ? this.messageService.error(error.error.detail)
            : error.error.message == 'JWT Token not found'
            ? this.messageService.alert('Sesion Expirada')
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
        },
      });
    }
  }

  cargartiempos() {

    if (this.tarea.estado != 'finalizado') return
    this.loadingTiempos = true;
    this.tareaService.getTiemposTimeTarea(this.tarea.id).subscribe({
      next: (response) => {
        this.loadingTiempos = false;
        this.tiempos = response;

        let segs = 0;
        let mins = 0;

        this.tiempos.forEach((tiempo) => {
          console.log(tiempo.total);
          const segundos = parseInt(
            (tiempo.total as number).toString().split('.')[1]
          );
          segs = segs + segundos;
          const minutos = parseInt(
            (tiempo.total as number).toString().split('.')[0]
          );
          mins = mins + minutos;
        });

        console.log('LOS MINUTOS => ', mins);
        console.log('LOS SEGUNDOS => ', segs);

        mins = mins + parseInt((segs / 60).toFixed(0));

        segs = segs - parseInt((segs / 60).toFixed(0)) * 60;

        console.log('LOS MINUTOS => ', mins);
        console.log('LOS SEGUNDOS => ', segs);

        const horas = parseInt((mins / 60).toFixed(0));

        mins = mins - parseInt((mins / 60).toFixed(0)) * 60;

        console.log('LAS HORAS => ', horas);
        console.log('LOS MINUTOS => ', mins);
        console.log('LOS SEGUNDOS => ', segs);

        this.totaltime = `${horas}h - ${mins}min - ${segs}s`;
      },
      error: (error) => {
        error.error?.detail
          ? this.messageService.error(error.error.detail)
          : error.error.message == 'JWT Token not found'
          ? this.messageService.alert('Sesion Expirada')
          : this.messageService.error('Ocurrio un error desconocido en local');
      },
    });
  }

  ngOnDestroy(): void {
    this.subcripciones.forEach((subcripcion) => {
      subcripcion.unsubscribe();
    });
  }
}
