import {
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  inject,
} from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  UntypedFormControl,
  Validators,
} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { ProductoService } from 'src/app/services/producto.service';
import { Producto } from 'src/app/interfaces/producto.interface';
import { AutoCompleteCompleteEvent } from 'primeng/autocomplete';
import { Location } from '@angular/common';
import { Mercancia } from 'src/app/interfaces/mercancia.interface';
import { MercanciaService } from 'src/app/services/mercancia.service';
import { Ubicacion } from 'src/app/interfaces/ubicaciones.interface';
import { UbicacionesService } from 'src/app/services/ubicaciones.service';
import { C } from '@fullcalendar/core/internal-common';
import { AlmacenesService } from 'src/app/services/almacenes.service';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'app-descargar-merca',
  templateUrl: './descargar-merca.component.html',
  styleUrls: ['./descargar-merca.component.scss'],
})
export class DescargarMercaComponent implements OnInit {
  constructor(private el: ElementRef) {}

  private readonly productoService = inject(ProductoService);
  private readonly almacenService = inject(AlmacenesService);
  private readonly mercanciaService = inject(MercanciaService);
  private readonly ubicacionService = inject(UbicacionesService);
  private readonly messageService = inject(CustomMessageService);
  private readonly Form = inject(FormBuilder);
  private readonly location = inject(Location);
  private readonly confirmationService = inject(ConfirmationService);
  private readonly router = inject(Router);

  // public listProductos: Producto[];
  public producto: Producto;
  public filteredProductos: Producto[] = [];

  public mercancias: Mercancia[] = [];
  public ubicaciones: Ubicacion[] = [];
  public filteredUbicaciones: Ubicacion[] = [];
  public filteredMercancias: Mercancia[] = [];

  public idProducto: number;
  public barcode: string;
  public codeInputLength: number = 0;
  public soloNumeros: boolean;
  public codigoValido: boolean = false;
  public codigoEanValido: boolean = false;
  public ubicacionValida: boolean = false;
  public ubicacionFocus: boolean = false;
  public almacenValida: boolean = false;
  public almacenFocus: boolean = false;
  public mercanciaValida: boolean = false;
  public mercanciaFocus: boolean = false;

  public loading = false;

  public mostrarFechaCaducidad: boolean = false;

  public formBajaMerca: FormGroup = this.Form.group({
    codigoEan: [
      null,
      [
        Validators.required,
        Validators.minLength(13),
        Validators.pattern('^[0-9]*$'),
      ],
    ],
    mercancia: [null, Validators.required],
    cantidad: [null, [Validators.required, Validators.min(0)]],
    ubicacion: [null, Validators.required],
    motivo: [null, Validators.required],
  });

  public hadSubmit: Boolean = false;

  public loadingFechaCaducidad: Boolean = false;
  public loadingMerca: Boolean = false;

  public timeout;

  // form getters ----------------------------------------------------------------------------------

  get codigoEan() {
    return this.formBajaMerca.get('codigoEan') as UntypedFormControl;
  }
  get mercancia() {
    return this.formBajaMerca.get('mercancia') as UntypedFormControl;
  }
  get cantidad() {
    return this.formBajaMerca.get('cantidad') as UntypedFormControl;
  }
  get ubicacion() {
    return this.formBajaMerca.get('ubicacion') as UntypedFormControl;
  }
  get motivo() {
    return this.formBajaMerca.get('motivo') as UntypedFormControl;
  }
  get formValue() {
    return this.formBajaMerca.value;
  }

  // end form getter --------------------------------------------------------------------------------

  ngOnInit(): void {
    // this.productoService.getProductos().subscribe({
    //   next: (resp) => {
    //     console.log(resp);
    //     this.listProductos = resp;
    //     this.filteredProductos = resp;
    //     this.cargarUbicaciones();
    //   },
    // });
    this.cargarUbicaciones();
  }

  back() {
    this.router.navigateByUrl('/');
  }

  validar(campo: string): boolean {
    if (campo === 'cantidad' || campo === 'precio') {
      if (this.formBajaMerca.get(campo)?.value > 0) {
        return this.formBajaMerca.get(campo)?.invalid;
      } else {
        return true;
      }
    }

    return this.formBajaMerca.get(campo)?.invalid;
  }

  cargarUbicaciones() {
    this.ubicacionService.getUbicaciones().subscribe({
      next: (res) => {
        console.log(res);
        this.ubicaciones = res;
        this.ubicaciones = this.ubicaciones.filter( ubi => ubi.tipo != 'temporal')
        // this.focusUbicacion();
      },
      error: (error) => {
        console.error(error);
        this.messageService.error(error.error.detail);
      },
    });
  }

  cargarMercancias() {
    console.log('CARGANDO MERCA');
    this.loadingMerca = true;
    this.mercanciaService
      .getMercanciasFromBarcodeUbi({
        codigoEan: this.barcode,
        ubicacionId: this.ubicacion.value,
      })
      .subscribe({
        next: (res) => {
          console.log(res);
          this.mercancias = res;
          this.loadingMerca = false;
          // this.focusMercancia();
        },
        error: (error) => {
          console.error(error);
          this.mercancias == null;
          this.mercancia.setValue(null);
          this.messageService.error(error.error.detail);
          this.loadingMerca = false;
        },
      });
  }

  submit(): void {
    this.hadSubmit = true;
    this.loading = true;
    if (this.formBajaMerca.invalid) {
      this.loading = false;
      this.messageService.error('Revise los campos');
      return;
    }

    const body = this.formValue;

    body.mercancia = body.mercancia.id;

    console.log(this.formValue);

    // llamada
    this.mercanciaService.descargarMercancia(body).subscribe({
      next: (res) => {
        console.log('MERCANCIA DESCARGADA', res);
        this.messageService.win('La mercancia se ha dado de baja con exito');
        this.formBajaMerca.reset();
        this.loading = false;
        this.hadSubmit = false;
        this.mercancias = null;
        this.codigoEanValido = false;
      },
      error: (error) => {
        console.error(error);
        this.messageService.error(error.error.detail);
        this.loading = false;
      },
    });

    console.log('enviado');
  }

  validarUbicacion(event) {
    setTimeout(() => {
      this.ubicacion.setValue(event.target.value);

      let ubicacionSeleccionada = this.ubicaciones.find(
        (ubi) => ubi.ubicacionId == this.ubicacion.value
      );

      if (ubicacionSeleccionada !== undefined) {
        this.ubicacionValida = true;
        if (this.codigoEanValido) {
          this.cargarMercancias();
        }
        // setTimeout(() => {
        //   console.log(this.cantidadInput.nativeElement);
        //   this.cantidadInput.nativeElement.focus();
        // }, 100);
      } else {
        this.ubicacionValida = false;
        ubicacionSeleccionada = null;
      }
    }, 100);
  }

  validarMercancia(newMercancia: string) {
    setTimeout(() => {
      this.mercancia.setValue(newMercancia);

      let mercanciaSeleccionada = this.mercancias.find(
        (mercancia) => mercancia.id == this.mercancia.value
      );

      if (mercanciaSeleccionada !== undefined) {
        this.mercanciaValida = true;
      } else {
        this.mercanciaValida = false;
        mercanciaSeleccionada = null;
      }
    }, 100);
  }

  validarCodigo(event) {
    this.barcode = event.target.value;

    this.codeInputLength = this.barcode.length;

    const regex = /^[0-9]*$/;
    this.soloNumeros = regex.test(this.barcode);

    // let productoSeleccionado = this.listProductos.find(
    //   (producto) => producto.codigoEan == this.barcode
    // );

    if (this.codeInputLength === 13 && this.soloNumeros) {
      this.codigoEanValido = true;
      if (this.ubicacionValida) {
        this.cargarMercancias();
      }
    } else {
      this.codigoEanValido = false;
    }
  }
}
