import { Component, OnDestroy, OnInit, ViewChild, inject } from '@angular/core';
import { FormBuilder, UntypedFormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { FileUpload } from 'primeng/fileupload';
import { Subscription } from 'rxjs';
import {
  Producto,
  ProductoComponente,
  UnidadMedida,
} from 'src/app/interfaces/producto.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { EnumService } from 'src/app/services/enum.service';
import { ProductoService } from 'src/app/services/producto.service';

@Component({
  selector: 'app-crear-producto',
  templateUrl: './crear-producto.component.html',
  styleUrls: ['./crear-producto.component.scss'],
})
export class CrearProductoComponent implements OnInit, OnDestroy {
  // servicios requeridos
  private readonly productoService = inject(ProductoService);
  private readonly activatedRoutes = inject(ActivatedRoute);
  private readonly router = inject(Router);
  private readonly messageService = inject(CustomMessageService);
  private readonly Form = inject(FormBuilder);
  private readonly enumService = inject(EnumService);

  public compuestoSnOptions: Object[] = [
    { value: 'Si', label: 'Si' },
    { value: 'No', label: 'No' },
  ];

  public tiposProductos: string[] = [];

  public hadSubmit: boolean = false;

  public loading = false;

  public loadingTipos = true;

  public componenteSeleccionadoControl: boolean = false;

  public unidadSeleccionadaControl: boolean = false;

  public componenteSeleccionado: ProductoComponente | null = null;

  public componentesPrevios: ProductoComponente[] = [];

  public unidadSeleccionada: any = {
    productoUnidadMedida: null,
    unidadMedida: null,
    nombre: null,
  };

  public unidadesPrevias: UnidadMedida[] = [];

  public cantidadAPoner: number | null = null;

  public productos: Producto[] | undefined;

  public selectComponents: ProductoComponente[];

  public selectProductosUnidades: Producto[];

  public selectSiNo: string[] = ['Si', 'No'];

  public barcode: number;

  public subs: Subscription[] = [];

  public ValorNulleable;

  public unidadesMedidasSN: string = 'No';

  public newProducto = this.Form.group({
    nombre: [null, Validators.required],
    descripcionInterna: [null, Validators.required],
    descripcionFactura: [null, Validators.required],
    compuestoSn: ['No'],
    unidadesSn: ['No'],
    loteSn: ['No'],
    componentes: [[]],
    unidadMedida: [[]],
    pvp: [null, Validators.required],
    iva: [21, Validators.required],
    codigoEan: [null],
    tipoProducto: [null, Validators.required],
  });

  // form getters ----------------------------------------------------------------------------------

  get nombre() {
    return this.newProducto.get('nombre') as UntypedFormControl;
  }
  get descripcionInterna() {
    return this.newProducto.get('descripcionInterna') as UntypedFormControl;
  }
  get descripcionFactura() {
    return this.newProducto.get('descripcionFactura') as UntypedFormControl;
  }
  get compuestoSn() {
    return this.newProducto.get('compuestoSn') as UntypedFormControl;
  }
  get unidadesSn() {
    return this.newProducto.get('unidadesSn') as UntypedFormControl;
  }
  get loteSn() {
    return this.newProducto.get('loteSn') as UntypedFormControl;
  }
  get componentes() {
    return this.newProducto.get('componentes') as UntypedFormControl;
  }
  get unidadMedida() {
    return this.newProducto.get('unidadMedida') as UntypedFormControl;
  }
  get pvp() {
    return this.newProducto.get('pvp') as UntypedFormControl;
  }
  get codigoEan() {
    return this.newProducto.get('codigoEan') as UntypedFormControl;
  }
  get tipoProducto() {
    return this.newProducto.get('tipoProducto') as UntypedFormControl;
  }

  // end form getter --------------------------------------------------------------------------------

  ngOnInit(): void {
    this.cargarTiposProductos();
    this.cargarProductosComponentes();
    this.cargarProductosUnidades();
    this.subs.push(
      this.activatedRoutes.params.subscribe((params) => {
        this.barcode = params['barcode'];
      })
    );
  }
    
  back() {
    this.router.navigateByUrl('/almacen/scanner');
  }

  validar(campo: string) {
    return this.newProducto.get(campo)?.valid;
  }

  esEntero(num: number) {
    if (num === null) return true;
    if (num === 0) return true;
    if (Number.isInteger(num)) {
      return true;
    } else return false;
  }

  // dinamica de los componentes y unidades -------------------------------------------------------------------

  seleccionarComponente() {
    this.componenteSeleccionadoControl = true;
  }

  seleccionarProductoUnidad(event) {
    this.unidadSeleccionada.nombre = event.value.nombre;
    this.unidadSeleccionada.productoUnidadMedida = event.value.id;
    this.unidadSeleccionadaControl = true;
  }

  agregarComponente() {
    if (this.componenteSeleccionado !== null) {
      this.componenteSeleccionado.cantidad = this.cantidadAPoner as number;

      let componenteAGuardar = JSON.parse(JSON.stringify(this.componenteSeleccionado))

      this.componentesPrevios.push(componenteAGuardar);

      this.cantidadAPoner = null;
      this.componenteSeleccionadoControl = false;
      this.componenteSeleccionado = null;
    }
  }

  agregarUnidades() {
    if (this.unidadSeleccionada !== null) {
      console.log(this.unidadSeleccionada);
      this.unidadesPrevias.push(this.unidadSeleccionada);

      console.log(this.unidadesPrevias);

      this.unidadSeleccionadaControl = false;
      this.unidadSeleccionada = {
        productoUnidadMedida: null,
        unidadMedida: null,
        nombre: null,
      };
      this.ValorNulleable = null;
    }
  }

  eliminarUnidad(unidad: UnidadMedida) {
    this.unidadesPrevias = this.unidadesPrevias.filter(
      (unidadP) => unidadP !== unidad
    );
  }

  eliminarComponente(componente: ProductoComponente) {
    this.componentesPrevios = this.componentesPrevios.filter(
      (component) => component !== componente
    );
  }

  submit() {
    this.hadSubmit = true;

    this.loading = true;

    if (
      this.newProducto.invalid ||
      (this.componentesPrevios.length === 0 &&
        this.newProducto.get('compuestoSn')?.value === 'Si')
    ) {
      console.log(this.newProducto.invalid);
      this.messageService.error('Verifique los campos');
      this.loading = false
      return;
    }

    if (this.newProducto.get('compuestoSn')?.value === 'No') {
      this.newProducto.get('componentes')?.setValue([] as any)
    }

    this.codigoEan.enable();

    let productoToSend: Producto = this.newProducto.value as Producto;

    if (this.componentesPrevios.length > 0) {
      productoToSend.componentes = this.componentesPrevios;
    } else productoToSend.componentes = null;

    productoToSend.codigoEan = this.barcode.toString();

    productoToSend.unidadesMedidas = this.unidadesPrevias;

    this.reqCreateProducto(productoToSend);
  }

  navigarAlScanner() {
    this.router.navigateByUrl(`productos/scanner`);
  }

  // llamadas al back -----------------------------------------------------------------------------

  // carga los componentes del select de componentes
  cargarProductosComponentes() {
    this.subs.push(
      this.productoService.getProductos().subscribe(
        (response) => {
          this.selectComponents = response;
          this.selectComponents = this.selectComponents.filter((component) => {
            return !this.componentesPrevios.includes(component);
          });
        },
        (error) => {
          console.error(error);
          this.messageService.error(error.error.detail);
        }
      )
    );
  }

  cargarProductosUnidades() {
    this.subs.push(
      this.productoService.getProductos().subscribe(
        (response) => {
          this.selectProductosUnidades = response;
        },
        (error) => {
          console.error(error);
          this.messageService.error(error.error.detail);
        }
      )
    );
  }

  // cargo los tipos de productos del select homonimo
  cargarTiposProductos() {
    this.subs.push(
      this.enumService.getEnum('tipo_producto').subscribe({
        next: res => {
          this.tiposProductos = res; 
          this.loadingTipos = false;
          console.log(this.tiposProductos);
        },
        error: error => this.messageService.error(error.error.detail)
      })
    )
  }

  reqCreateProducto(productoToSend: Producto) {
    this.subs.push(
      this.productoService.createProducto(productoToSend).subscribe(
        (response) => {
          console.log(response);
          this.messageService.add({
            severity: 'success',
            summary: 'Exito!',
            detail: 'Producto creado con exito!',
          });
          console.log(response);
          this.router.navigateByUrl('/productos/scanner');
        },
        (error) => {
          console.error(error);

          this.loading = false;
          this.messageService.error(error.error.detail);
          return;
        }
      )
    );
  }

  ngOnDestroy(): void {
    this.subs.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }
}