import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  UntypedFormControl,
  Validators,
} from '@angular/forms';
import { bO } from '@fullcalendar/core/internal-common';
import { error } from 'console';
import { Subscription } from 'rxjs';
import { Presupuesto } from 'src/app/interfaces/presupuesto.interface';
import { Producto } from 'src/app/interfaces/producto.interface';
import { Ubicacion } from 'src/app/interfaces/ubicaciones.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { EnumService } from 'src/app/services/enum.service';
import { PresupuestoService } from 'src/app/services/presupuesto.service';
import { UbicacionesService } from 'src/app/services/ubicaciones.service';

@Component({
  selector: 'app-devoluciones',
  templateUrl: './devoluciones.component.html',
  styleUrls: ['./devoluciones.component.scss'],
})
export class DevolucionesComponent implements OnInit, OnDestroy {
  private readonly messageService = inject(CustomMessageService);
  private readonly Form = inject(FormBuilder);
  private readonly ubicacionesService = inject(UbicacionesService);
  private readonly presupuestoService = inject(PresupuestoService);
  private readonly enumService = inject(EnumService);

  private subs: Subscription[] = [];

  public hadSubmit: boolean = false;
  public loading: boolean = false;
  public loadingCantidad: boolean = false;
  public loadingUbicaciones: boolean = false;
  public loadingPresupuestos: boolean = false;
  public loadingProductosPrespuesto: boolean = false;

  public timeout;

  public cantidadOptenida: number = null;

  public mostrarInputPresupuesto = true;
  public mostrarInputsProducto = false;
  public mostrarConfirmacion = false;

  public ubicaciones: Ubicacion[];
  public presupuestos: Presupuesto[];
  public ProductosDelPresupuesto: Producto[];

  public selectEstado = [];

  public selectPresValue = null;
  public barcodeValue = null;

  public productoSelected = null;
  public productoConLote = null;

  public formDevolucion: FormGroup = this.Form.group({
    ubicacion: [null, Validators.required],
    presupuesto: [null, Validators.required],
    codigoEan: [null, Validators.required],
    cantidad: [null, Validators.required],
    estado: [null, Validators.required],
    numeroLote: [null],
  });

  get ubicacion() {
    return this.formDevolucion.get('ubicacion') as UntypedFormControl;
  }
  get presupuesto() {
    return this.formDevolucion.get('presupuesto') as UntypedFormControl;
  }
  get codigoEan() {
    return this.formDevolucion.get('codigoEan') as UntypedFormControl;
  }
  get cantidad() {
    return this.formDevolucion.get('cantidad') as UntypedFormControl;
  }
  get estado() {
    return this.formDevolucion.get('estado') as UntypedFormControl;
  }
  get numeroLote() {
    return this.formDevolucion.get('numeroLote') as UntypedFormControl;
  }

  ngOnInit(): void {
    this.cargarPresupuestos();
    this.cargarUbicaciones();
    this.cargarEstados();
  }

  validar(campo) {
    return this.formDevolucion.get(campo).valid;
  }

  esEntero(num: number) {
    if (num === null) return true;
    if (num === 0) return true;
    if (Number.isInteger(num)) {
      return true;
    } else return false;
  }

  esPositivo(num: number) {
    if (num < 0) {
      return false;
    }
    return true;
  }

  validarCantidad() {
    const cant = this.cantidad.value;
    if (!this.esEntero(cant)) {
      return 1;
    }
    if (!this.esPositivo(cant)) {
      return 2;
    }
    if (cant === 0) return 3;
    if (this.cantidad.value > this.cantidadOptenida) return 4;

    return 0;
  }

  cargarEstados() {
    this.subs.push(
      this.enumService.getEnum('estado_devolucion').subscribe({
        next: (res) => (this.selectEstado = res),
        error: (error) => this.messageService.error(error.error.detail),
      })
    );
  }

  mostrarPresupuesto() {
    this.mostrarInputPresupuesto = true;
    this.mostrarInputsProducto = false;
    this.mostrarConfirmacion = false;
    this.cargarPresupuestos();
  }
  mostrarProducto() {
    if (!this.presupuesto.value && !this.ubicacion.value) return;
    this.mostrarInputPresupuesto = false;
    this.mostrarInputsProducto = true;
    this.mostrarConfirmacion = false;

    this.productoSelected = null;
    this.selectPresValue = null;
    this.barcodeValue = null;
    this.numeroLote.setValue(null);
    this.cargarProductosByPrespuesto();
  }
  mostrarConfirm() {
    if (
      !this.productoSelected ||
      (this.barcodeValue &&
        this.productoSelected.numeroLote &&
        !this.productoConLote)
    )
      return;
    this.mostrarInputPresupuesto = false;
    this.mostrarInputsProducto = false;
    this.mostrarConfirmacion = true;

    this.cantidad.setValue(null);
    this.estado.setValue(null);
    this.cantidadOptenida = this.productoSelected.cantidadSalida;
    console.log('HADSUBMIT => ', this.hadSubmit);
  }

  cargarProductosByPrespuesto() {
    this.loadingProductosPrespuesto = true;
    this.subs.push(
      this.presupuestoService
        .getProductosByPrespuesto(this.presupuesto.value)
        .subscribe({
          next: (res) => {
            console.log('PRESUPUESTOS => ', res);
            this.ProductosDelPresupuesto = res;
            this.loadingProductosPrespuesto = false;
          },
          error: (error) => {
            console.error(error);
            this.messageService.error(error.error.detail);
            this.loadingPresupuestos = false;
          },
        })
    );
  }

  cargarUbicaciones() {
    this.loadingUbicaciones = true;
    this.subs.push(
      this.ubicacionesService.getUbicacionesDeEntrada().subscribe({
        next: (res) => {
          console.log('UBICACIONES => ', res);
          this.ubicaciones = res;
          this.loadingUbicaciones = false;
        },
        error: (error) => {
          console.error(error);
          this.messageService.error(error.error.detail);
          this.loadingUbicaciones = false;
        },
      })
    );
  }
  cargarPresupuestos() {
    this.loadingPresupuestos = true;
    this.subs.push(
      this.presupuestoService.getPresupuestos().subscribe({
        next: (res) => {
          console.log('PRESUPUESTOS => ', res);
          this.presupuestos = res;
          this.loadingPresupuestos = false;
        },
        error: (error) => {
          console.error(error);
          this.messageService.error(error.error.detail);
          this.loadingPresupuestos = false;
        },
      })
    );
  }

  limpiarSelect() {
    console.log('pasa algo ean');
    this.selectPresValue = null;
    this.productoSelected = this.ProductosDelPresupuesto.find(
      (producto) => producto.codigoEan === this.barcodeValue
    );
    this.codigoEan.setValue(null);
    if (this.productoSelected) {
      this.codigoEan.setValue(this.productoSelected.codigoEan);

      if (this.productoSelected.numeroLote) console.log('CON LOTEEEEEEEE');
    }
  }

  limpiarCodigoEan() {
    console.log('pasa algo select');
    this.barcodeValue = null;
    this.codigoEan.setValue(this.selectPresValue.codigoEan);
    this.productoSelected = this.selectPresValue;
  }

  buscandoConNumeroLote() {
    this.productoConLote = this.ProductosDelPresupuesto.find((producto) => {
      return (
        //@ts-ignore
        producto.numeroLote == this.numeroLote.value &&
        producto.codigoEan == this.barcodeValue
      );
    });
  }

  submit() {
    this.hadSubmit = true;

    if (
      this.formDevolucion.invalid ||
      !this.cantidadOptenida ||
      this.validarCantidad() > 0
    ) {
      this.messageService.error('Revise los campos');
      return;
    }

    this.loading = true;

    const devolucionToSend = this.formDevolucion.value;

    devolucionToSend.tipo = this.productoSelected.tipo;
    devolucionToSend.producto = this.productoSelected.id;

    this.subs.push(
      this.presupuestoService
        .registrarDevolucionDePresupuesto(devolucionToSend)
        .subscribe({
          next: (res) => {
            console.log('LA DEVOLUCION => ', res);
            this.messageService.win('Devolucion registrada con exito');
            this.hadSubmit = false;
            this.mostrarProducto();
            this.loading = false;
          },
          error: (error) => {
            console.error(error);
            this.messageService.error(error.error.detail);
            this.loading = false;
          },
        })
    );
  }

  finalizar() {
    const body = {
      rrhh: JSON.parse(localStorage.getItem('usuario')).id,
      presupuesto: this.presupuesto.value,
    };

    console.log('BODY => ', body);
    this.presupuestoService.finalizarDevfiolucion(body).subscribe({
      next: (res) => {
        console.log(res);
        this.mostrarPresupuesto();
        this.messageService.win(res.mensaje);
      },
      error: (error) => {
        console.error(error);
        this.messageService.error(error.error.detail);
      },
    });
  }

  ngOnDestroy(): void {
    this.subs.forEach((sub) => {
      sub.unsubscribe();
    });
  }
}
